.experience {
  &__item {
    padding: 0px 0px 10px 0px;
    &__time, &__location {
      color: $font-secondary;
      float: right;
      clear: right;
    }
    &__company {
      display: inline-block;
      font-style: italic;
    }
    &__position {
      display: inline-block;
      font-weight: 600;
    }
    &__description {
      padding-top: 4px;
      width: 75vw;
    }
  }
}